"use strict";

export default function(sequelize, DataTypes) {
  return sequelize.define("WorkerLoad", {
    _id: {
      type: DataTypes.INTEGER,
      primaryKey: true,
      autoIncrement: true 
    },
    allocated: {
      type: DataTypes.INTEGER,
      allowNull: false
    },
    weekenum:  {
      type: DataTypes.INTEGER,
      allowNull: false
    }
  }, {
    classMethods: {
      associate:function(models){
        models.Workerload.belongsTo( models.Worker );
      }
    }
  });

};

